body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#root{display:flex;flex-direction:column;margin:50px}.login-container{position:absolute;top:0;right:0;left:0;bottom:0;display:flex;flex-direction:column;grid-gap:20px;align-items:center;justify-content:center}.login-container .MuiFormControl-root{width:300px}.logout-container{margin-top:50px;text-align:right}.header-container{display:flex;grid-gap:20px;margin-bottom:50px}@media only screen and (max-width: 1099px){.header-container{flex-direction:column}}.header-info{display:grid;grid-gap:20px;grid-template-columns:repeat(2, 1fr);align-items:center;width:100%}@media only screen and (max-width: 600px){.header-info{grid-template-columns:repeat(1, 1fr)}}.side-content{display:flex;grid-gap:20px;align-items:center;width:100%}@media only screen and (max-width: 600px){.side-content{flex-direction:column}.side-content .left-container{width:100%}.side-content .right-container{width:100%}}.side-content .left-container{display:flex;grid-gap:20px;flex-direction:column}.side-content .right-container{flex:2 1}.side-content .right-container .MuiFormControl-root{width:100%;margin-bottom:20px}.table-container{display:flex}.column-controls,.row-controls{display:flex}.column-controls svg,.row-controls svg{opacity:0.6;cursor:pointer}.column-controls svg:hover,.row-controls svg:hover{opacity:1}.row-controls{flex-direction:column}.input-table .MuiTableCell-root{padding:0}.input-table .MuiFormControl-root{width:100%;margin-bottom:-1px;min-width:150px}.input-table .MuiFormControl-root input{text-align:center}.input-table .table-controls{min-width:120px}.MuiTableContainer-root{background-color:rgba(0,0,0,0.09) !important;border:1px solid #aaaaaa !important}.MuiTableContainer-root .MuiTableCell-root{border:1px solid #aaaaaa !important}.check{margin-right:10px;color:#53ad56 !important}.cross{margin-right:10px;color:#ea453f !important}.spinner{display:flex;justify-content:center;align-items:center;margin-bottom:20px}.lds-roller{display:inline-block;position:relative;width:80px;height:80px}.lds-roller div{animation:lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;transform-origin:40px 40px}.lds-roller div:after{content:' ';display:block;position:absolute;width:7px;height:7px;border-radius:50%;background:#3f51b5;margin:-4px 0 0 -4px}.lds-roller div:nth-child(1){animation-delay:-0.036s}.lds-roller div:nth-child(1):after{top:63px;left:63px}.lds-roller div:nth-child(2){animation-delay:-0.072s}.lds-roller div:nth-child(2):after{top:68px;left:56px}.lds-roller div:nth-child(3){animation-delay:-0.108s}.lds-roller div:nth-child(3):after{top:71px;left:48px}.lds-roller div:nth-child(4){animation-delay:-0.144s}.lds-roller div:nth-child(4):after{top:72px;left:40px}.lds-roller div:nth-child(5){animation-delay:-0.18s}.lds-roller div:nth-child(5):after{top:71px;left:32px}.lds-roller div:nth-child(6){animation-delay:-0.216s}.lds-roller div:nth-child(6):after{top:68px;left:24px}.lds-roller div:nth-child(7){animation-delay:-0.252s}.lds-roller div:nth-child(7):after{top:63px;left:17px}.lds-roller div:nth-child(8){animation-delay:-0.288s}.lds-roller div:nth-child(8):after{top:56px;left:12px}@keyframes lds-roller{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

